.main {
   flex: 1;
}
.bg-dark {
   background-color: #12002b;
   min-height: calc(100vh - 143px);
   padding: 1px 0;
}
.sign-in-content {
   box-sizing: border-box;
   background-color: white;
   width: 300px;
   margin: 0 auto;
   margin-top: 3rem;
   padding: 2rem;
}
.sign-in-icon {
   font-size: 5rem;
}
.sign-in-button {
   display: block;
   width: 100%;
   padding: 8px;
   font-size: 1.1rem;
   font-weight: bold;
   margin-top: 1rem;
   border-color: #00bc77;
   background-color: #00bc77;
   color: #fff;
}
.input-wrapper {
   display: flex;
   flex-direction: column;
   text-align: left;
   margin-bottom: 1rem;
}
.input-wrapper label {
   font-weight: bold;
}
.input-wrapper input {
   padding: 5px;
   font-size: 1.2rem;
}
.confirm-success p {
   margin: 15px 0 0 0;
}
.error-message {
   color: red;
   margin-bottom: 0;
}