.main {
   flex: 1;
}
.bg-dark {
   background-color: #12002b;
   min-height: calc(100vh - 143px);
   padding: 1px 0;
}
.header {
   color: #fff;
   margin-bottom: 2rem;
}
.sr-only {
   border: 0;
   clip: rect(1px, 1px, 1px, 1px);
   -webkit-clip-path: inset(50%);
   clip-path: inset(50%);
   height: 1px;
   margin: -1px;
   overflow: hidden;
   padding: 0;
   position: absolute;
   width: 1px;
   white-space: nowrap;
}