.sr-only {
   border: 0;
   clip: rect(1px, 1px, 1px, 1px);
   -webkit-clip-path: inset(50%);
   clip-path: inset(50%);
   height: 1px;
   margin: -1px;
   overflow: hidden;
   padding: 0;
   position: absolute;
   width: 1px;
   white-space: nowrap;
}
.main-nav {
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 5px 20px;
}
.main-nav a {
   font-weight: bold;
   color: #2c3e50;
}
.main-nav-item {
   text-decoration: none;
   margin-right: 0.5rem;
}
.main-nav-item:hover {
   text-decoration: underline;
}
.main-nav-logo {
   display: flex;
   align-items: center;
}
.main-nav-logo-image {
   max-width: 100%;
   width: 200px;
}